import { Component, HostListener, TemplateRef, ViewChild, OnInit, ElementRef, Renderer2 } from '@angular/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { MatDialog } from '@angular/material/dialog';
import { FormArray, FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from 'ngx-toastr';
import { DataService } from 'src/app/shared/services/data-service';
import { Configuration } from 'src/app/app.configuration';
import { forkJoin, of, Observable, Subject, fromEvent, throwError } from 'rxjs';
import { catchError, finalize, debounceTime, map, distinctUntilChanged, filter } from 'rxjs/operators';
import { Location } from '@angular/common';
import { MatDialogRef } from '@angular/material/dialog';
import * as _ from 'lodash';


@Component({
  selector: 'app-survey-list',
  templateUrl: './survey-list.component.html',
  styleUrls: ['./survey-list.component.scss']

})
export class SurveyListComponent implements OnInit {

  createQuestionForm: FormGroup
  isLoading = false;
  _id: any
  template_id: any
  question_id: any
  editHeader = false;
  dateField: string = 'createdDate';
  totalItems: number = 0;
  currentPage: number = 1;
  itemsPerPage: number = 10;
  questionList: any = [];
  visibleData: any[] = [];
  @ViewChild('dialogContent', { static: true }) dialogContent!: TemplateRef<any>;
  @ViewChild('deletedialog', { static: true }) deletedialog!: TemplateRef<any>;
  @ViewChild('scrollContainer', { static: true }) scrollContainer!: ElementRef;
  @ViewChild('questionsContainer', { static: false }) questionsContainer: ElementRef;
  @ViewChild('inputField') inputField: ElementRef;
  destroy$: Subject<void> = new Subject<void>();
  subPopUp: any;
  subPopUp1: any;
  payQuestions: any[];
  isCreate: boolean = true;
  data: any
  prompt_id: string;
  el: any;
  lastInput: any;
  questionInfo: any[];
  visibleDataa: any[];


  constructor(
    private dialog: MatDialog,
    private fb: FormBuilder,
    private router: Router,
    private configuration: Configuration,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private dataService: DataService,
    private renderer: Renderer2,
    private location: Location,
    // private dialogRef: MatDialogRef<>,
  ) { }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnInit(): void {
    this.createQuersion();
    // this.setupScrollListener();
    this.getSurveyList();
  }
  shouldLoadMore(): boolean {
    const scrollElement = this.scrollContainer.nativeElement;
    return (
      scrollElement.scrollTop + scrollElement.clientHeight >=
      scrollElement.scrollHeight - 10
    );
  }

  onScroll() {
    const scrollElement = this.scrollContainer.nativeElement;

    if (
      scrollElement.scrollTop + scrollElement.clientHeight >=
      scrollElement.scrollHeight - 10 // add a small buffer to prevent precision issues
    ) {
      this.getSurveyList();
    }
  }

  createQuersion() {
    this.createQuestionForm = this.fb.group({
      templateName: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(1000)]],
      questions: this.fb.array([this.createQuestionFormGroup()]),
      prompt: ['', Validators.required],
      _id: [''],
      template_id: [''],
      question_id: [''],
      prompt_id: [''],
    });
  }

  createQuestionFormGroup(questionData?: any): FormGroup {
    return this.fb.group({
      question: [questionData ? questionData.question : '', [Validators.required, Validators.minLength(5), Validators.maxLength(1000)]],
      _id: [questionData ? questionData._id : ''],
      updatedDate: [questionData ? questionData.updatedDate : ''],
      position: [questionData ? questionData.position : ''],

    });
  }

  // Custom function to capitalize the first letter of a string
  capitalizeFirstLetter(input: string): string {
    if (input.length === 0) return input; // Return the input if it's an empty string
    return input.charAt(0).toUpperCase() + input.slice(1);
  }


  get questions() {
    return (this.createQuestionForm.get('questions') as FormArray);
  }

  addQuestions(questionData?: any) {
    const questionsArray = this.createQuestionForm.get('questions') as FormArray;

    // Check if the last question is non-empty, then add a new question field
    const lastQuestion = questionsArray.at(questionsArray.length - 1);
    if (!lastQuestion || lastQuestion.value.question.trim() !== '') {
      const questionFormGroup = this.createQuestionFormGroup(questionData);
      questionsArray.push(questionFormGroup);

      // Mark the entire form as touched to trigger validation
      this.createQuestionForm.markAllAsTouched();
    }
    // Scroll to the last question
    if (this.questions.length > 3) {
      this.scrollToLastQuestion();
    }
  }

  bulkupdate() {
    const payload = {
      templateName: this.questionList._id,
      question_data: this.questionList.updatedDate
    };
    this.dataService.bulkUpdate(this.configuration.bulkUpdate, payload).subscribe(data => {

    })
  }

  scrollToLastQuestion() {
    setTimeout(() => {
      const container = this.questionsContainer.nativeElement;
      const lastQuestion = container.lastElementChild;
      if (lastQuestion) {
        this.renderer.setProperty(container, 'scrollTop', lastQuestion.offsetTop);
      }
    });
  }

  openDialogWithTemplateRef(templateRef: TemplateRef<any>) {
    this.createQuersion()
    this.subPopUp = this.dialog.open(templateRef, {
      width: '70%',
      disableClose: true,
      data: { contentTemplate: templateRef },
    });
    this.subPopUp.afterClosed().subscribe(() => {
      this.getSurveyList();
    })
  }

  openDialogWithTemplate(templateRef: TemplateRef<any>) {
    this.subPopUp1 = this.dialog.open(templateRef, {
      width: '20%',
      disableClose: true,
      data: { contentTemplate: templateRef },
    });
  }

  onDrop(event: CdkDragDrop<string[]>) {
    const previousIndex = event.previousIndex;
    const currentIndex = event.currentIndex;
    if (previousIndex !== currentIndex) {
      // const draggedItem = this.visibleData[previousIndex];
      moveItemInArray(this.visibleData, previousIndex, currentIndex);
      // draggedItem.updatedDate = new Date();
      // this.saveItems()
      let maxPosition = -1;
      let maxPositionIndex = -1;

      // Loop through the list
      for (let i = 0; i < this.visibleData.length; i++) {
        const currentPosition = this.visibleData[i].position;

        // Check if the current position is greater than the maximum
        if (currentPosition > maxPosition) {
          maxPosition = currentPosition;
          maxPositionIndex = i;
        }
      }
      // Now, maxPositionIndex holds the index of the object with the largest position
      const LargestPosition = this.visibleData[maxPositionIndex];
      var x = LargestPosition.position;
      for (let data of this.visibleData) {
        data.position = x;
        x = x - 1;
      }

      const payload = this.visibleData.map(item => ({
        id: item._id,
        position: item.position,
      }));


      this.dataService.bulkUpdate<any>(this.configuration.bulkUpdate, payload)
        .subscribe(response => {
          this.getSurveyList();
        }, error => {
        });
    }
  }

  onDrops(event: CdkDragDrop<{ value: string }[]>) {
    let previousIndex = event.previousIndex;
    let currentIndex = event.currentIndex;
    let data = event.item.data.value;
    if (previousIndex !== currentIndex) {
      moveItemInArray(this.questions.controls, previousIndex, currentIndex);
      // Update the order of questions in the questionInfo array
      moveItemInArray(this.questionInfo, previousIndex, currentIndex)
      this.saveItemsQues()
      const payload = this.questionInfo.map(item => ({
        id: item._id,
        position: item.position,
      }));
      this.dataService.questionbulkUpdate<any>(this.configuration.questionbulkUpdate, payload)
        .subscribe(response => {

        }, error => {
        });
    }
  }
  saveItemsQues() {
    this.questionInfo.map((sample, index) => {
      sample.position = index + 1;
    });

  }

  onInputChange(event: Event) {
    // You can access the changed value using event.target.value
    const changedValue = (event.target as HTMLInputElement).value;
    // Perform actions or update your component's state based on the changed value
    this.createQuestionForm.get('templateName').setValue(changedValue);
  }

  onSaveClick() {
    this.markFormGroupTouched(this.createQuestionForm);
    const questionsArray = this.createQuestionForm.get('questions') as FormArray;
    const templateNameControl = this.createQuestionForm.get('templateName');

    if (templateNameControl.hasError('required')) {
      this.toastr.error('Template Name is required');
      return;
    }

    // Check if the templateName field violates the minimum length constraint
    if (templateNameControl.hasError('minlength')) {
      this.toastr.warning('Template Name must be at least 5 characters long');
      return;
    }

    // Check if the templateName field violates the maximum length constraint
    if (templateNameControl.hasError('maxlength')) {
      this.toastr.warning('Template Name cannot exceed 1000 characters');
      return;
    }

    // Check for Question
    for (let i = 0; i < questionsArray.length; i++) {
      const questionFormGroup = questionsArray.at(i) as FormGroup;
      const questionControl = questionFormGroup.get('question');
      const question = questionControl.value;

      if (questionControl.hasError('required')) {
        this.toastr.error(`Question ${i + 1} is required`);
        return;
      }

      const stringLength = question.length;

      if (stringLength < 5) {
        this.toastr.warning(`Question ${i + 1} must be at least 5 characters long`);
        return;
      }

      if (stringLength > 1000) {
        this.toastr.warning(`Question ${i + 1} cannot exceed 1000 characters`);
        return;
      }
    }
    const promptControl = this.createQuestionForm.get('prompt');
    // Check for Prompt
    if (promptControl.hasError('required')) {
      this.toastr.error('Prompt is required');
      return;
    }
    if (this.createQuestionForm.invalid) {
      this.toastr.error('Please fill out all required fields');
      return;
    }
    // Check if there are no questions or if all questions are empty
    const ques = questionsArray.value;
    this.payQuestions = [];
    ques.forEach(element => {
      this.payQuestions.push({
        question_name: element.question,
        question: element.question
      })
    });
    const payload = {
      templateName: this.createQuestionForm.value.templateName,
      question_data: this.payQuestions,
      prompt: this.createQuestionForm.value.prompt
    }


    this.dataService.addQuestion(this.configuration.survey, payload).subscribe(data => {

      var sample = data;
      this.questionList = sample;
      this.toastr.success(this.questionList.message);
      this.getSurveyList()
      window.location.reload();
      this.cancel();
      // window.location.reload();

    }, (error) => {
      if (error.status === 400) {
        this.toastr.error('This template already exists');
      } else {
        this.toastr.error('An error occurred while saving.');
      }
    }
    );
  }

  saveAsCopy() {
    this.markFormGroupTouched(this.createQuestionForm);

    const templateNameControl = this.createQuestionForm.get('templateName');

    if (templateNameControl.hasError('required')) {
      this.toastr.error('Template Name is required');
      return;
    }

    // Check if the templateName field violates the minimum length constraint
    if (templateNameControl.hasError('minlength')) {
      this.toastr.warning('Template Name must be at least 5 characters long');
      return;
    }

    // Check if the templateName field violates the maximum length constraint
    if (templateNameControl.hasError('maxlength')) {
      this.toastr.warning('Template Name cannot exceed 1000 characters');
      return;
    }

    const questionsArray = this.createQuestionForm.get('questions') as FormArray;
    const ques = questionsArray.value;

    if (!ques.length || ques.every(q => !q.question.trim())) {
      this.toastr.error('At least one question is required');
      return;
    }

    // Check if all questions meet length requirements
    for (const question of ques) {
      const stringLength = question.question.trim().length;
      if (stringLength < 5 || stringLength > 1000) {
        this.toastr.warning('Question length must be between 5 and 1000 characters');
        return;
      }
    }

    this.payQuestions = ques.map(element => {
      return {
        question_name: element.question,
        question: element.question
      };
    });

    const payload = {
      templateName: this.createQuestionForm.value.templateName,
      question_data: this.payQuestions,
      prompt: this.createQuestionForm.value.prompt
    };

    this.dataService.addQuestion(this.configuration.survey, payload).subscribe(data => {
      this.questionList = data;
      this.toastr.success(this.questionList.message);
      // this.getSurveyList();
      window.location.reload();
    }, (error) => {
      if (error.status === 400) {
        this.toastr.error('This template already exists');
      } else {
        this.toastr.error('An error occurred while saving.');
      }
    });
  }

  getSurveyList() {
    this.spinner.show();
    const sortBy = 'createdDate';  // Sort initially by createdDate
    const orderBy = 'desc';
    const pageSize = this.itemsPerPage; // Set your desired items per page
    const service = `${this.configuration.survey}?sortBy=${sortBy}&orderBy=${orderBy}&pageSize=${pageSize}&pageNumber=${this.currentPage}`;

    this.dataService.getQuestionList(service).subscribe((result: any) => {
      if (result.isSuccess) {
        const newItems = result.data.filter((item: any) => item.isActive === true);

        // Filter out duplicates
        const uniqueNewItems = newItems.filter(item => !this.visibleData.some(existingItem => existingItem._id === item._id));

        // Check if the returned data is not empty before updating visibleData
        if (newItems.length > 0) {
          // Add new items to the beginning of the visibleData array
          this.visibleData = [...uniqueNewItems, ...this.visibleData];

          const sortedDataArray = [...this.visibleData];
          const sortedArray = _.sortBy(this.visibleData, 'position').reverse();
          // For example, if you want to update this.visibleData with the sorted array:
          this.visibleData = sortedArray;
          this.currentPage++;
          this.toastr.success(result.message);
        } else {
          // You can handle the end of the list, e.g., show a message or perform other actions
        }
      } else {
        throw new Error(result.message || 'Error fetching survey list');
      }

      this.spinner.hide();
    });
  }

  deleteTemplate(_id: string) {
    this.spinner.show();

    this.dataService.deleteTemplate(this.configuration.survey, _id).subscribe(
      data => {
        this.toastr.success('Template Successfully Deleted!!');
        window.location.reload();
        //  this.getSurveyList()
        this.spinner.hide();
      },
      error => {
        this.toastr.error(error.error.message),
          this.spinner.hide();
      }
    );
  }

  deleteQuestion(_id: string, index: number) {
    this.spinner.show();
    const questionsArray = this.createQuestionForm.get('questions') as FormArray;
    if (questionsArray.length === 1) {
      this.toastr.warning('At least one question is required.');
      this.spinner.hide();
      return;
    }
    if (_id && _id.trim() !== '') {
      // Delete from the server if a non-empty _id is provided
      this.dataService.deleteQues(this.configuration.question, _id).subscribe(
        (result: any) => {

          if (typeof result === 'object' && 'data' in result) {
            const deletedTemplateId = result.data.template_id;
            this.toastr.success('Question Successfully Deleted!!');
            this.getQuestions(deletedTemplateId);
            this.spinner.hide();
          } else {
            this.toastr.error('An error occurred while deleting the question.');
          }
        },
        (error) => {
          this.toastr.error('An error occurred while deleting the question.');
        }
      );
    } else {
      // Remove the question at the specified index from the local form array
      this.deleteQuestions(index);
      this.spinner.hide();
    }
  }

  deleteQuestions(index: number) {
    const questionsArray = this.createQuestionForm.get('questions') as FormArray;

    // Remove the question at the specified index
    questionsArray.removeAt(index);
  }

  // Helper function to mark all form controls as touched recursively
  markFormGroupTouched(formGroup: FormGroup) {
    Object.values(formGroup.controls).forEach(control => {
      control.markAsTouched();

      if (control instanceof FormGroup) {
        this.markFormGroupTouched(control);
      }
    });
  }

  getQuestions(templateId: string) {
    this.spinner.show();
    this.dataService.getById(this.configuration.survey, templateId).subscribe((result: any) => {
      if (result.isSuccess) {
        const isActiveData = result.data.question.filter((item: any) => item.isActive === true);
        if (isActiveData.length > 0) {
          const templateInfo = result.data.temple;
          const promptInfo = result.data.prompt.length ? result.data.prompt[0].prompt : '';
          this.questionInfo = isActiveData; // Use filtered data
          // Clear existing questions in the form array
          const questionsArray = this.createQuestionForm.get('questions') as FormArray;
          questionsArray.clear();
          // Add new controls for each question
          this.questionInfo.forEach((question: any) => {
            questionsArray.push(this.createQuestionFormGroup(question));
          });
          this.questionInfo.sort((a, b) => {
            return a.position - b.position;
          });

          // Log visibleData
          // Update the form controls with sorted data
          this.questionInfo.forEach((question: any, index: number) => {
            questionsArray.at(index).patchValue(question);
          });
          const templateNames = this.questionInfo.map(item => item._id);

          const templateId = templateInfo._id;
          const promptId = result.data.prompt.length ? result.data.prompt[0]._id : '';
          this.createQuestionForm.patchValue({
            templateName: templateInfo.templateName,
            prompt: promptInfo,
            _id: templateId,
            prompt_id: promptId,
            template_id: templateId
          });
        }
      }
      this.spinner.hide();
    }, error => this.spinner.hide());
  }

  updateQuestions(question_id: any) {
    const questionsArray = this.createQuestionForm.get('questions') as FormArray;
    const errorMessages: string[] = [];

    for (let i = 0; i < questionsArray.length; i++) {
      const questionFormGroup = questionsArray.at(i) as FormGroup;
      const questionControl = questionFormGroup.get('question');
      const question = questionControl.value;

      // Check if question is empty
      if (questionControl.hasError('required')) {
        errorMessages.push(`Question ${i + 1} is required`);
      }

      const stringLength = question.length;

      // Check minimum length constraint
      if (stringLength < 5) {
        errorMessages.push(`Question ${i + 1} must be at least 5 characters long`);
      }

      // Check maximum length constraint
      if (stringLength > 1000) {
        errorMessages.push(`Question ${i + 1} cannot exceed 1000 characters`);
      }
    }

    if (errorMessages.length > 0) {
      // Show all error messages
      errorMessages.forEach(message => this.toastr.error(message));
      return;
    }

    // Continue with the rest of the update logic...

    const questionsToUpdate = questionsArray.value.map((questionControl: any) => {
      if (questionControl._id) {
        return {
          question_name: questionControl.question,
          question: questionControl.question,
          id: questionControl._id,
          template_id: this.createQuestionForm.value.template_id,
        };
      } else {
        return {
          id: "",
          template_id: this.createQuestionForm.value.template_id,
          question_name: questionControl.question,
          question: questionControl.question
        };
      }
    });

    this.dataService.updateQuestion(question_id, this.configuration.question, questionsToUpdate)
      .pipe(
        catchError(error => {
          this.toastr.error('Error occurred while updating questions.');
          return of(null);
        })
      )
      .subscribe((result: any) => {

        if (result.warningMessage) {
          this.toastr.warning(result.warningMessage);
        }

        window.scrollTo(0, 0);
      });
  }

  updateTemplate(template_id: any) {
    const templateNameControl = this.createQuestionForm.get('templateName');
    const templateName = templateNameControl.value;

    // Check if template name is empty
    if (!templateName) {
      this.toastr.warning('Template name cannot be empty');
      return;
    }

    // Check if the templateName field violates the minimum length constraint
    if (templateNameControl.hasError('minlength')) {
      this.toastr.warning('Template name must be at least 5 characters long');
      return;
    }

    // Check if the templateName field violates the maximum length constraint
    if (templateNameControl.hasError('maxlength')) {
      this.toastr.warning('Template name cannot exceed 1000 characters');
      return;
    }

    const payload = {
      templateName: templateName
    };

    this.dataService.updateTemplate(template_id, this.configuration.survey, payload)
      .pipe(
        catchError(error => {
          if (error && error.status === 400) {
            // Handle the case where the template name already exists
            this.toastr.error('Template name already exists. Please choose a different name.');
            return throwError('Template name already exists. Please choose a different name.'); // Stop the function execution
          } else {
            // Handle other errors
            this.toastr.error('Error occurred while updating the template.');
            throw new Error('Error occurred while updating the template.'); // Stop the function execution
          }
        })
      )
      .subscribe((result: any) => {

        if (result && result.warningMessage) {
          this.toastr.warning(result.warningMessage);
        } else {
          // this.toastr.success('Updating Successfuly');
        }
        window.scrollTo(0, 0);

      });
  }
  // Similar modifications can be made to other functions...
  updatePrompt(prompt_id: any) {
    const payload = {
      prompt: this.createQuestionForm.value.prompt
    };

    this.dataService.updatePrompt(prompt_id, this.configuration.prompt, payload)
      .pipe(
        catchError(error => {
          this.toastr.error('Error occurred while updating the prompt.');
          return of(null);
        })
      )
      .subscribe((result: any) => {
      });
  }

  updateAll() {
    const question_id = this.createQuestionForm.value.question_id;
    const prompt_id = this.createQuestionForm.value.prompt_id;
    const template_id = this.createQuestionForm.value.template_id;
    this.updateQuestions(question_id);
    this.updatePrompt(prompt_id);
    this.updateTemplate(template_id);
    if (this.createQuestionForm.invalid) {
      this.toastr.error('Form is invalid. Please fill in all required fields.');
      return;
    } else {
      this.toastr.success('Updating Successfully');
      this.getSurveyList()
      setTimeout(() => {
        window.location.reload();
      }, 1000); // 1000 milliseconds = 1 second
    }

  }

  cancel() {
    this.dialog.closeAll();
  }

  cancelSaveAsPopup() {
    this.subPopUp1.close();
  }

  refresh() {
    window.location.reload();
  }

}
